@font-face {
  font-family: 'Superscratchy';
  src: local('Superscratchy'),
       url(./assets/fonts/Superscratchy.ttf) format('truetype');
}

body {
  margin: 0;
  font-family: Superscratchy;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #9da9bb;
  text-align: left;
  background-color: #061325;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1, h2, h3, h4, h5, h6, i{
  font-family: Superscratchy;
  color: rgb(216, 226, 239);
}

h1{
  font-size: 48px;
}

.img-container{
  width: 34.5%;
  padding: 0px 15px;
}

.text-container{
  width: 49%;
  padding: 0px 15px;
}

.img-logo{
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.faq,
.gremplin{
  padding: 3rem 0;
  align-items: center;
  justify-content: center;
  display: flex;
  text-align: center;
}

.faq p,
.gremplin p{
  font-size: 20px;
  color: rgb(216, 226, 239);
  text-align: center;
}

.gridImage{
  display: flex;
  flex-wrap: wrap;
  
}

.imgBox{
  width: 50%;
  height: 50%;
  padding: 20px;
}

.gremplin-img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.member-img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 20px;
}

.member-img-box{
  width: 120px;
  height: 120px;
  object-fit: cover;
  margin: 0px 7.5px;
}

.about-img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 20px;
}

.members{
  padding-top: 50px;
  padding-bottom: 30px;
}

.member{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding-bottom: 50px;
}

.member-img{
  margin-bottom: 10px;
}

@media (min-width: 1080px) {
  .link-os a{
    text-align: center;
      z-index: 10;
      width: 17%;
      height: 20%;
      left: 39%;
      top: 34%;
      position: absolute;
      /* border: 2px solid white; */
  }
  
  .price{
    text-align: center;
    z-index: 10;
    width: 20%;
    /* align-items: center; */
    left: 38%;
    top: 83.5%;
    position: absolute;
  }
  
  .price span{
    display: flex;
    flex-direction: row;
    justify-content: center;
    color: #e9e1dc;
    margin-bottom: .5rem;
  }
  
  .price span i{
    margin-right: 10px;
    font-size: 1.75rem;
    display: flex;
    align-items: center;
    height: 34px;
  }
  
  .price h3,
  .price h1,
  .price span i{
    color: #e9e1dc;
  }
  
  .price h1{
    font-size: 54px;
  }
  
  .footer-container{
    display: flex;
    flex-direction: row;
    border-top: solid 1px hsla(0,0%,100%,.1);
    padding-top: 20px;
    padding-bottom: 20px;
    margin-top: 20px;
  }
  
  .footer-container div{
    padding: 0px;
  }
  
  .logo-footer{
    margin-right: 10px;
  }
  
  .footer-container .col-md-9{
    text-align: left;
    vertical-align: middle;
  }
  
  .footer-container .col-md-9 p{
    line-height: 24px;
  }
}

